/* HomePage.scss */

$primaryColor: #007bff;
$secondaryColor: #c3bdbd;
$accentColor: #ff9800;

.homepage {
    text-align: center;
    padding: 2rem;

    header {
        margin-top: 5rem;
        margin-bottom: 5rem;

        h1 {
            font-size: 5rem;
            color: $primaryColor;
            margin-bottom: 0.5rem;
        }

        p {
            font-size: 1.6rem;
            color: $secondaryColor;
        }
    }

    .features {

        color: $accentColor;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        gap: 8rem;
        background-color: #1d1d2c;




        .feature {
            height: 40rem;
            margin-bottom: 10rem;
            border: #007bff 1px solid;
            width: 100vw;
            background-color: #3d3d5594;
            padding: 5rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
            background-size: cover;
            background-repeat: no-repeat;
            object-fit: cover;

            .card {
                background-color: #a3a3c994;
                border-radius: 25px;
                width: 70%;
                display: flex;
                justify-content: space-evenly;
                .home_paragraph {
                    font-size: 1.6rem;
                    text-align: left;
                    width: 40%;
                    // margin-left: auto;
                    padding: 5rem;
                    margin-top: 0;

                    
                }
                .home_image{
                    width: 40%;
                }

            }


            h3 {
                font-size: 2.5rem;
                color: $primaryColor;
                margin-bottom: 1rem;
            }

            p {
                font-size: 1.6rem;
                color: $secondaryColor;
                margin-top: 1rem;
            }
        }
    }



    .cta {
        margin-top: 3rem;
        width: 70%;
        margin-left: auto;
        margin-right: auto;

        h2 {
            font-size: 3rem;
            color: $primaryColor;
        }

        p {
            font-size: 1.8rem;
            color: $secondaryColor;
            margin-top: 1rem;
        }

        button {
            background-color: $primaryColor;
            color: white;
            padding: 1.5rem 3rem;
            border: none;
            border-radius: 5px;
            cursor: pointer;
            font-size: 1.8rem;
            transition: background-color 0.3s;

            &:hover {
                background-color: $accentColor;
            }
        }
    }

    footer {
        margin-top: 3rem;
        color: $secondaryColor;

        p {
            font-size: 1.4rem;
            margin-bottom: 0.5rem;

            span {
                text-decoration: underline;
                cursor: pointer;

                &:hover {
                    color: darkred;
                }
            }
        }
    }
}